.game-area{
  position: relative;
  margin: 50px auto;
  width: 600px;
  height:  600px;
  border: 2px solid black;
}
.snake-dot{
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: black;
  border: 1px solid white;
  z-index: 2;
}
.snake-food{
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: rgb(250, 1, 1);
  border: 1px solid white;
  z-index: 1;
}